
.font-size-xsmall {
    font-size: x-small;
}

.font-size-small {
    font-size: small;
}

.font-size-medium {
    font-size: medium;
}

.font-size-large {
    font-size: large;
}

.font-size-xlarge {
    font-size: x-large;
}

.font-size-xxlarge {
    font-size: xx-large;
}

.boader-gray {
    border-bottom: 2px solid #E5E5E5;
}

.boader-black {
    border-bottom: 1px solid #000000;
}

.horizontal-center {
    display: flex;
    justify-content: center;
}

.horizontal-left {
    display: flex;
    justify-content: left;
}

.horizontal-right {
    display: flex;
    justify-content: right;
}

.vertical-center {
    display: flex;
    align-items: center;
}

.text-left {
    display: flex;
    align-items: center;
    justify-content: left;
    height: 100%;
}

.text-right {
    display: flex;
    align-items: center;
    justify-content: right;
    height: 100%;
}

.space-2px {
    display: flex;
    height: 2px;
}

.space-3px {
    display: flex;
    height: 3px;
}

.space-10px {
    display: flex;
    height: 10px;
}

.space-20px {
    display: flex;
    height: 20px;
}

.space-30px {
    display: flex;
    height: 30px;
}

.space-40px {
    display: flex;
    height: 40px;
}

.space-50px {
    display: flex;
    height: 50px;
}

.gap {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.full-column {
    width: 100%;
}
