.custom-table {
    border-collapse: collapse;
    width: 100%;
    font-size: large;
}

.custom-table td,
.custom-table th {
    padding: 8px;
    text-align: left;
    border-top: 1px solid #ddd;
    /* 상단 세로 보더 추가 */
    border-bottom: 1px solid #ddd;
    /* 하단 세로 보더 추가 */
    border-right: 1px solid #ddd;
    /* 우측 가로 보더 추가 */
}

.custom-table td:first-child,
.custom-table th:first-child {
    border-left: none;
    background-color: #f2f2f2;
    /* 첫 번째 열에만 백그라운드 컬러 추가 */
    width: 15%;
}

.custom-table td:nth-child(2),
.custom-table th:nth-child(2) {
    width: 25%;
    border-right: none;
}

.custom-table td:nth-child(3),
.custom-table th:nth-child(3) {
    width: 50%;
    border-right: none;
}

.custom-table td:last-child,
.custom-table th:last-child {
    border-right: none;
}

.whole-page-ui {
    padding: 10px 30px 30px;
}

.custom-table2 {
    border-collapse: collapse;
    width: 100%;
    font-size: large;
}

.custom-table2 td,
.custom-table2 th {
    padding: 8px;
    text-align: center;
    border-top: none;
    border-bottom: none;
    border-left: none;
    /* 좌측 보더 제거 */
    border-right: none;
    /* 우측 보더 제거 */
    border-top: 1px solid #ddd;
    /* 상단 가로 보더 추가 */
    border-bottom: 1px solid #ddd;
    /* 하단 가로 보더 추가 */
}

.custom-table2 td:first-child,
.custom-table2 th:first-child {
    background-color: #f2f2f2;
    width: 10%;
}

.custom-table2 td:nth-child(2),
.custom-table2 th:nth-child(2) {
    width: 10%;
}

.custom-table2 td:nth-child(3),
.custom-table2 th:nth-child(3) {
    width: 10%;
}

.custom-table2 td:nth-child(4),
.custom-table2 th:nth-child(4) {
    width: 25%;
}

.custom-table2 td:nth-child(5),
.custom-table2 th:nth-child(5) {
    width: 25%;
}

.custom-table2 td:nth-child(6),
.custom-table2 th:nth-child(6) {
    width: 10%;
}

.custom-table2 td:last-child,
.custom-table2 th:last-child {
    border-right: none;
    width: 10%;
}

.space-10px {
    display: flex;
    height: 10px;
}

.dropdown-cell {
    position: relative;
    /* 부모의 position을 relative로 설정 */
}

.text-background-round {
    background-color: #91E94D;
    /* 배경 색상 설정 */
    border-radius: 8px;
    /* 테두리를 둥글게 만드는 값 설정 */
    margin: 10px;
}

.custom-button {
    background-color: #91E94D;
    width: 100%;
    border: none;
    touch-action: none;
}

.custom-button:hover {
    background-color: #91E94D;
}

.custom-button:active {
    background-color: #91E94D;
    /* 버튼을 누를 때 색상을 원래 색상으로 유지 */
}

.custom-button:focus {
    outline: none;
    /* 포커스 스타일 제거 */
    background-color: #91E94D;
    /* 버튼이 포커스를 받을 때 색상을 원래 색상으로 유지 */
}

.custom-button.clicked {
    background-color: #91E94D; /* 버튼을 누를 때 색상을 원래 색상으로 유지 */
  }